import { clone } from './utils'
import { Compiler, parseJSONPath } from './JSONPathCompiler'

export default function actionCreatorFactory(stateGetter, collect){
  return (keyPath, fn, opts) => {
    const options = Object.assign({deps:[]}, opts)
    const compiler = new Compiler(keyPath)
    const matcher = compiler.createMatcher()
    const depMatchers = options.deps.map((k) => (new Compiler(k)).createMatcher())
    return (...payloads) => {
      let $ = stateGetter()
      let depValues = depMatchers.map((depMatcher) => depMatcher($, payloads).map((r) => r.value))
      return matcher($, payloads).map((result) => {
        const setter = (newValue) => {
          var oldCur = $
          var newCur = clone($)
          var cur = newCur
          result.pwd.slice(1).forEach((key) => {
            if(key !== null){
              cur[key] = clone(oldCur[key])
              cur = cur[key]
              oldCur = oldCur[key]
            }
          })
          if(result.name !== null){
            cur[result.name] = newValue
            collect(newCur, result.pwd)
          }else{
            collect(newValue, result.pwd)
          }
        }
        const args = payloads.concat(depValues).concat([result.value, setter])
        return fn.apply(result, args)
      })
    }
  }
}



